import * as React from "react"
import Layout from '../../components/Layout'
import pdficon from "../../resource/pdf-icon.png"
import mtrspec from "../../pdf/MTRSPEC.pdf"
import mtreq from "../../pdf/MTRSPEC.pdf"
import doc from "../../pdf/MTRSPEC.pdf"
import p1 from "../P1"

const p3Track = ({ location }) => {
  return (
    <Layout>
      <h1>part tracking</h1>
      <br />
      <h2>Second Stage Piston-3</h2>
      <br />
      <h3>Production steps</h3>
      <div class="jutifycenter">

        <div class="trackinglistbox">

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked />
            <label> 1: Material Spec.</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked/>
            <label> 2: Part Technical Drawing (dwg) </label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked/>
            <label> 3: Material Supply</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked/>
            <label> 4: Production Process</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked/>
            <label> 5: Delivry Inspedction</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly checked/>
            <label> 6: Installation</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly />
            <label> 7: Performance Evaluation inspection</label>
            <br />
          </div>

          <div class="checkbox">
            <input type="checkbox" disabled readonly />
            <label> 8: In Use</label>
            <br />
          </div>

        </div>
      </div>
      <br />
      <h3>Downloads</h3>
      <div class="jutifycenter">

      
      <a href={mtrspec} download>
        <div class="align-icon">
          <img src={pdficon}></img>
          <h4> 1: Material Spec Sheet</h4>
        </div>
      </a>

      <a href={mtrspec} download>
        <div class="align-icon">
          <img src={pdficon}></img>
          <h4> 2: Material Requirement Sheet</h4>
        </div>
      </a>
      
      <a href={mtrspec} download>
        <div class="align-icon">
          <img src={pdficon}></img>
          <h4> 3: Part ID Document</h4>
        </div>
      </a>

      </div>


    </Layout>
  )
}

export default p3Track;